import { collection, addDoc, getDocs, doc, deleteDoc } from "firebase/firestore";
import { db } from "./firebaseSetup.js";

// Firestoreにデータを追加
async function addUrl(url, title, category, userId) {
    try {
        const docRef = await addDoc(collection(db, "urls"), { url, title, category, createdAt: new Date(), userId });
        console.log("Document added with ID:", docRef.id);
    } catch (error) {
        console.error("Error adding document:", error);
    }
}

// Firestoreからデータを取得
async function fetchUrls() {
    try {
        const querySnapshot = await getDocs(collection(db, "urls"));
        return querySnapshot.docs.map(doc => ({ id: doc.id, data: doc.data() }));
    } catch (error) {
        console.error("Error fetching documents:", error);
        return [];
    }
}

// Firestoreのデータを削除
async function deleteUrl(docId) {
    try {
        await deleteDoc(doc(db, "urls", docId));
        console.log(`Document ${docId} deleted successfully!`);
    } catch (error) {
        console.error("Error deleting document:", error);
    }
}

export { addUrl, fetchUrls, deleteUrl };