export async function getPreview(url) {
    const API_KEY = "479caf1751a010ec074397313794465c";  
    const apiUrl = `https://api.linkpreview.net/?key=${API_KEY}&q=${encodeURIComponent(url)}`;

    try {
        const res = await fetch(apiUrl);
        const data = await res.json();

        console.log("APIのレスポンス:", data);  // ✅ ここでログが出力されるか確認！

        return data.image || "デフォルト画像URL";  
    } catch (error) {
        console.error("サムネイル取得エラー:", error);
        return "デフォルト画像URL";
    }
}